<template>
  <div>
    <div style='height: 100%; background-color: #ffff;'>
      <v-toolbar color="cyan" dark height="50">
        <v-toolbar-title style="color: white; font-size: 18px;">Qun l vn ti quc t</v-toolbar-title>
      </v-toolbar>
      <div class='mb-3'>
      </div>
      <div>
        <v-layout row wrap>
          <v-flex xs12 sm12 class='text-xs-center'>
            <div class='mb-3'>
              <v-chip label :color="stateTabQl !== 'doanh_nghiep_van_tai' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('doanh_nghiep_van_tai')">
                Doanh nghip vn ti
              </v-chip>
              <v-chip label :color="stateTabQl !== 'phuong_tien' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('phuong_tien')">
                Phng tin
              </v-chip>
              <v-chip label :color="stateTabQl !== 'phuong_tien2' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('phuong_tien2')">
                Cp giy php ln 2 tr i
              </v-chip>
              <v-chip label :color="stateTabQl !== 'giay_phep_van_tai_quoc_te' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('giay_phep_van_tai_quoc_te')">
                Giy php vn ti quc t
              </v-chip>
              <v-chip label :color="stateTabQl !== 'giay_phep_lien_van' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('giay_phep_lien_van')">
                Giy php lin vn
              </v-chip>
              <!-- <v-chip label :color="stateTabQl !== 'phu_hieu_lien_van' ? 'orange' : 'blue darken-1'" text-color='white' class='mr-2' @click="changeTab('phu_hieu_lien_van')">
                Ph hiu lin vn
              </v-chip> -->
            </div>
          </v-flex>
        </v-layout>
      </div>
    </div>
    <v-app>
      <router-view></router-view>
    </v-app>
  </div>
</template>

<script>
  import router from '@/router'
  export default {
    data: () => ({
      workingUnitList: [],
      currentIndex: 0,
      loading: true,
      listgroupHoSoFilterItems: [],
      indexListStatus: -1,
      currentItemTab: 'tab2',
      tabs: [
        {
          text: 'Tra cu h s',
          value: 'tab1',
          state: 'tat_ca_ho_so'
        },
        {
          text: 'X l h s',
          value: 'tab2',
          state: 'xu_ly_ho_so'
        },
        {
          text: 'Qun l giy php',
          value: 'tab3',
          state: 'quan_ly_giay_phep'
        },
        {
          text: 'Qun l vn ti quc t',
          value: 'tab4',
          state: 'quan_ly_van_tai_quoc_te'
        }
      ],
      stateTab: ''
    }),
    computed: {
      configPage () {
        return this.$store.getters.configPage
      },
      stateTabQl () {
        return this.$store.getters.stateTabQl
      }
    },
    created () {
      var vm = this
      vm.$nextTick(function () {
        vm.loadGroupHoSoFilter()
      })
    },
    watch: {
      '$route': function (newRoute, oldRoute) {
        let vm = this
        let currentQuery = newRoute.query
        if (currentQuery.hasOwnProperty('reloadGroup')) {
          vm.loadGroupHoSoFilter()
        }
      }
    },
    methods: {
      changeTab (state) {
        var vm = this
        if (state === 'doanh_nghiep_van_tai') {
          router.push('/doanh-nghiep-van-tai')
          vm.$store.commit('setStateTabQl', 'doanh_nghiep_van_tai')
        } else if (state === 'phuong_tien') {
          router.push('/phuong-tien')
          vm.$store.commit('setStateTabQl', 'phuong_tien')
        } else if (state === 'phuong_tien2') {
          router.push('/phuong-tien?gp=true')
          vm.$store.commit('setStateTabQl', 'phuong_tien2')
        } else if (state === 'giay_phep_van_tai_quoc_te') {
          router.push('/giay-phep-van-tai-quoc-te')
          vm.$store.commit('setStateTabQl', 'giay_phep_van_tai_quoc_te')
        } else if (state === 'giay_phep_lien_van') {
          router.push('/giay-phep-lien-van')
          vm.$store.commit('setStateTabQl', 'giay_phep_lien_van')
        } else {
          router.push('/phu-hieu-lien-van')
          vm.$store.commit('setStateTabQl', 'phu_hieu_lien_van')
        }
      },
      menuTabChange (item, event) {
        var vm = this
        console.log('------------', item)
        vm.stateTab = item.state
        if (item.state === 'tat_ca_ho_so') {
          vm.$store.commit('setStatusParamFilter', '')
          vm.$store.commit('setSubstatusParamFilter', '')
          let params = {
            status: '',
            substatus: '',
            start: 0,
            end: 15,
            sort: 'modified',
            order: 'false',
            follow: true
          }
          vm.$store.dispatch('loadDanhSachHoSo', params).then(results => {
          }).catch(xhr => {
          })
          router.push('/')
          vm.onScrollTop()
        } else if (item.state === 'quan_ly_giay_phep') {
          router.push('/doanh-nghiep-van-tai')
        } else if (item.state === 'quan_ly_van_tai_quoc_te') {
          router.push('/doanh-nghiep-van-tai')
        }
      },
      groupHoSoFilter (item, index) {
        var vm = this
        console.log('item status', item)
        vm.indexListStatus = index
        let statusParamFilter = ''
        let substatusParamFilter = ''
        vm.loadGroupHoSoFilter()
        if (item.id !== 'tra_cuu') {
          vm.$store.commit('setHoSoLableTitle', item.title)
          // vm.hoso_title_table = item.title
          if (item.level === 1) {
            statusParamFilter = item.idSub
            substatusParamFilter = item.id
          } else {
            statusParamFilter = item.id
            substatusParamFilter = item.idSub
          }
        }
        vm.$store.commit('setStatusParamFilter', statusParamFilter)
        vm.$store.commit('setSubstatusParamFilter', substatusParamFilter)
        let params = {
          status: statusParamFilter,
          substatus: substatusParamFilter,
          start: 0,
          end: 15,
          sort: 'modified',
          order: 'false',
          follow: true
        }
        vm.$store.dispatch('loadDanhSachHoSo', params).then(results => {
        }).catch(xhr => {
        })
        router.push('/danh-sach-ho-so/' + index)
        vm.onScrollTop()
      },
      loadGroupHoSoFilter () {
        // var vm = this
        // vm.$store.dispatch('loadGroupHoSoFilter', null).then(results => {
        //   vm.listgroupHoSoFilterItems = results
        // })
      },
      onScrollTop (e) {
        window.scrollBy(0, -99999)
      }
    }
  }
</script>



