<template>
  <div>
    <v-layout row wrap style="background-color: #fff; margin: 0;">
      <v-flex xs12 sm3>

      </v-flex>
      <v-flex xs12 sm6 style='display: inline-flex; '>
        <v-text-field
        placeholder='Tm kim theo s GPKDVT/ Tn doanh nghip'
        v-model='keywordsSearchGPVTQT'
        style='width : 350px;'
        :disabled="loadingGiayPhepVanTaiQte"
        @keyup.enter='searchKeyWordGPVTQT'
        clearable
        ></v-text-field>
        <v-btn small :disabled='loadingGiayPhepVanTaiQte' class='' color='primary' @click.native='searchKeyWordGPVTQT'>
          Tm kim
        </v-btn>
        <v-btn small :disabled='loadingGiayPhepVanTaiQte' class='' color='primary' @click.native='dialogSearch = !dialogSearch'>
          Tm kim nng cao
        </v-btn>
      </v-flex>
      <v-flex xs12 sm3>

      </v-flex>
      <v-flex xs12 sm12 class='mt-3'>
        <v-data-table
        :headers='giayPhepVanTaiQteHeaders'
        :items='giayPhepVanTaiQteItems'
        :loading="false"
        hide-actions
        class='table-landing table-bordered mx-3'
        no-data-text='Khng c giy php no!'
        v-if='!loadingGiayPhepVanTaiQte'>
        <template slot='headers' slot-scope='props'>
          <tr>
            <th v-for='(header, index) in props.headers' :key='header.text' class="text-xs-center">
              <span v-if='index === 6' v-html='header.text'></span>
              <span v-else>{{ header.text }}</span>
            </th>
          </tr>
        </template>
        <template slot='items' slot-scope='props'>
          <td style='padding: 8px; padding-left: 0px; width: 3%;' class='text-xs-center'>{{ hosoDatasPage * 15 - 15 + props.index + 1 }}</td>
          <td style='padding: 8px; width: 9%;' class='text-xs-center'>
            {{ props.item.sogiayphep }}
          </td>
          <td style='padding: 8px; width: 15%;' class='text-xs-center'>
            {{ props.item.noicap }}
          </td>
          <td style='padding: 8px; width: 12%;' class='text-xs-center'>
            {{ props.item.ngaycap }}
          </td>
          <td style='padding: 8px; width: 12%;' class='text-xs-center'>
            {{ props.item.ngayhethan }}
          </td>
          <td style='padding: 8px; width: 10%;' class='text-xs-center'>
            {{ props.item.trangthai }}
          </td>
          <td style='padding: 8px;' class='text-xs-center'>
            {{ props.item.dnvantai }} <br>
            {{ props.item.loaihinhkinhdoanh }}
          </td>
          <td style='padding: 8px;' class='text-xs-center'>
            <v-tooltip top class='mr-1'>
              <a href="javascript:;" @click="downloadGP(props.item.download)" slot="activator">
                <v-icon style="font-size: 18px; cursor: pointer; font-weight: bold;">vertical_align_bottom</v-icon>
              </a>
              <span>Download</span>
            </v-tooltip>
          </td>
          <!-- <td style='padding: 8px;' class='text-xs-center'>
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer'>directions_car</v-icon>
              <span>Phng tin</span>
            </v-tooltip>
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer'>directions</v-icon>
              <span>Tuyn</span>
            </v-tooltip>
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer'>visibility</v-icon>
              <span>Xem</span>
            </v-tooltip>
          </td> -->
        </template>
      </v-data-table>
      <div v-else>
        <!-- <v-progress-circular
        :size='100'
        :width='5'
        color='green'
        indeterminate
        ></v-progress-circular> -->
        <content-placeholders class='mt-1'>
          <content-placeholders-text :lines='10' />
        </content-placeholders>
      </div>
        <tiny-pagination class='mt-2 mx-3' style='float: right;' :total='hosoDatasTotal' :page='hosoDatasPage' custom-class='custom-tiny-class' @tiny:change-page='paggingData' ></tiny-pagination>
      </v-flex>
    </v-layout>
  </div>
</template>
<script>
import router from '@/router'
import Vue from 'vue/dist/vue.min.js'
import TinyPagination from './pagging/hanghai_pagination.vue'
import $ from 'jquery'
import toastr from 'toastr'
export default {
  props: ['index'],
  components: {
    'tiny-pagination': TinyPagination
  },
  data: () => ({
    govAgencys: [],
    btnLoading: false,
    giayPhepVanTaiQteHeaders: [
      {
        text: 'STT',
        align: 'center',
        sortable: false,
        value: 'stt'
      },
      {
        text: 'S giy php',
        align: 'center',
        sortable: true,
        value: 'so_giay_phep'
      },
      {
        text: 'Ni cp',
        align: 'center',
        sortable: true,
        value: 'noi_cap'
      },
      {
        text: 'Ngy cp',
        align: 'center',
        sortable: true,
        value: 'ngay_cap'
      },
      {
        text: 'Ngy ht hn',
        align: 'center',
        sortable: true,
        value: 'ngay_het_han'
      },
      {
        text: 'Trng thi',
        align: 'center',
        sortable: true,
        value: 'trang_thai'
      },
      {
        text: 'Doanh nghip vn ti <br> Loi hnh kinh doanh',
        align: 'center',
        sortable: true,
        value: 'sogpkd'
      },
      {
        text: 'Download',
        align: 'center',
        sortable: true,
        value: 'download'
      }
      // {
      //   text: 'Thao tc',
      //   align: 'center',
      //   sortable: true,
      //   value: 'thaoTac'
      // }
    ],
    giayPhepVanTaiQteItems: [],
    hosoDatasTotal: 1,
    hosoDatasPage: 1,
    keywordsSearchGPVTQT: '',
    loadingGiayPhepVanTaiQte: false
  }),
  computed: {
    loading () {
      return this.$store.getters.loading
    }
  },
  created () {
    var vm = this
    vm.loadingGiayPhepVanTaiQuocTe({
      start: 0,
      end: 15
    })
    vm.$store.commit('setStateTabQl', 'giay_phep_van_tai_quoc_te')
  },
  watch: {
  },
  methods: {
    searchKeyWordGPVTQT () {
      var vm = this
      vm.loadingGiayPhepVanTaiQuocTe({
        start: 0,
        end: 15
      })
    },
    loadingGiayPhepVanTaiQuocTe (data) {
      var vm = this
      let params = {
        keyword: vm.keywordsSearchGPVTQT
      }
      if (data) {
        for (var key in data) {
          params[key] = data[key]
        }
      }
      vm.loadingGiayPhepVanTaiQte = true
      vm.$store.dispatch('loadGiayPhepVanTai', params).then(results => {
        vm.giayPhepVanTaiQteItems = results.data
        vm.hosoDatasTotal = parseInt(results.total)
        vm.loadingGiayPhepVanTaiQte = false
      }).catch(xhr => {
      })
    },
    paggingData (config) {
      var vm = this
      vm.loadingGiayPhepVanTaiQuocTe({
        start: (config.page - 1) * 15,
        end: (config.page - 1) * 15 + 15
      })
      vm.hosoDatasPage = config.page
    },
    downloadGP (url) {
      var vm = this
      if (!url) {
        toastr.error('Khng tm thy file giy php.')
        return
      }
      window.open(url, '_blank')
    }
  },
  filters: {
  }
}
</script>



