<template>
  <div>
    <v-layout row wrap style="background-color: #fff; margin: 0;">
      <v-flex xs12 sm12>
        <v-dialog v-model="thongTinXNCDialog" max-width="900" transition="fade-transition">
          <v-card>
            <v-card-title class="headline">Thng tin xut nhp cnh</v-card-title>
            <v-btn icon dark class="mx-0 my-0 absolute__btn_panel mr-2" @click.native="thongTinXNCDialog = false">
              <v-icon>clear</v-icon>
            </v-btn>
            <div>
              <add-phuong-tien :id="registrationNumber"></add-phuong-tien>
            </div>
          </v-card>
        </v-dialog>
      </v-flex>
      <v-flex xs12 sm3>
        
      </v-flex>
      <v-flex xs12 sm6 style="display: inline-flex; ">
        <v-text-field
        placeholder="Nhp bin kim sot"
        v-model="keywordsSearchPT"
        style="width : 350px;"
        @keyup.enter="searchPT"
        :disabled="loadingPhuongTien"
        clearable
        ></v-text-field>
        <v-btn small :disabled="loadingPhuongTien" class="" color="primary" @click="searchPT">
          Tm kim
        </v-btn>
        <v-btn small :disabled="loadingPhuongTien" class="" color="primary" @click="dialogSearch = !dialogSearch">
          Tm kim nng cao
        </v-btn>
      </v-flex>
      <v-flex xs12 sm3>

      </v-flex>
      <v-flex xs12 sm12 class="mt-3">
        <v-data-table
        :headers="phuongTienHeaders"
        :items="phuongTienItems"
        :loading="false"
        hide-actions
        v-if="!loadingPhuongTien"
        class="table-landing table-bordered mx-3"
        no-data-text="Khng c phng tin no!">
        <template slot='headers' slot-scope='props'>
          <tr>
            <th v-for="(header, index) in props.headers" :key="header.text" class="text-xs-center">
              <span>{{ header.text }}</span>
            </th>
          </tr>
        </template>
        <template slot="items" slot-scope="props">
          <td style="padding: 8px; padding-left: 0px;width: 3%; " class="text-xs-center">{{ hosoDatasPage * 15 - 15 + props.index + 1 }}</td>
          <td style="padding: 8px; width: 10%; cursor: pointer;" class="text-xs-center" @click="toDetail(props.item)">
            {{ props.item.bienkiemsoat }} 
          </td>
          <td style="padding: 8px; cursor: pointer;" class="text-xs-center" @click="toDetail(props.item)">
            {{ props.item['nhanhieu'] }} <br>
            {{ props.item['tendangky'] }} 
          </td>
          <td style="padding: 8px; width: 12%; cursor: pointer;" class="text-xs-center" @click="toDetail(props.item)">
            {{ props.item.sokhung }} 
          </td>
          <td style="padding: 8px; width: 12%; cursor: pointer;" class="text-xs-center" @click="toDetail(props.item)">
            {{ props.item.somay }} 
          </td>
          <td style="padding: 8px; width: 12%; cursor: pointer;" class="text-xs-center" @click="toDetail(props.item)">
            {{ props.item['loaiphuongtien'] }} 
          </td>
          <td style="padding: 8px; width: 15%; cursor: pointer;" class="text-xs-center" @click="toDetail(props.item)">
            <div class="mb-2" v-for="(item, index) in props.item['xuanhapcanh']">
              <span>{{item['thoidiemnhapcanh']}}</span> 
              <br>
              <span>{{item['thoidiemxuatcan']}}</span>
              <br>
              <v-divider></v-divider>
            </div>
          </td>
          <td style="padding: 8px; width: 9%;" class="text-xs-center">
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer' @click="toDetail(props.item, 'phuongtien_anchor')">directions_car</v-icon>
              <span>Phng tin</span>
            </v-tooltip>
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer' @click="toDetail(props.item, 'tuyen_anchor')">directions</v-icon>
              <span>Tuyn</span>
            </v-tooltip>
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer' @click="toDetail(props.item)">visibility</v-icon>
              <span>Xem</span>
            </v-tooltip>
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer' @click="viewHistoryXNC(props.item)">assignment_ind</v-icon>
              <!-- <img slot='activator' style="position: absolute; top: 0; cursor: pointer;" src='../store/visitor.png' height="16" width="16" @click="viewHistoryXNC(props.item)"/> -->
              <span>Thng tin xut nhp cnh</span>
            </v-tooltip>
          </td>
        </template>
      </v-data-table>
      <div v-else>
        <!-- <v-progress-circular
        :size="100"
        :width="5"
        color="green"
        indeterminate
        ></v-progress-circular> -->
        <content-placeholders class="mt-1">
          <content-placeholders-text :lines="10" />
        </content-placeholders>
      </div>
      <tiny-pagination class="mt-2 mx-3" style="float: right;" :total="hosoDatasTotal" :page="hosoDatasPage" custom-class="custom-tiny-class" 
          @tiny:change-page="paggingData" ></tiny-pagination>
      </v-flex>
    </v-layout>
  </div>
</template>
<script>
import router from '@/router'
import TinyPagination from './pagging/hanghai_pagination.vue'
import AddPhuongTien from './AddPhuongTien.vue'
export default {
  props: ['index'],
  components: {
    'tiny-pagination': TinyPagination,
    'add-phuong-tien': AddPhuongTien
  },
  data: () => ({
    govAgencys: [],
    btnLoading: false,
    registrationNumber: 0,
    thongTinXNCDialog: false,
    phuongTienHeaders: [
      {
        text: 'STT',
        align: 'center',
        sortable: false,
        value: 'stt'
      },
      {
        text: 'Bin kim sot',
        align: 'center',
        sortable: false,
        value: 'bien_kiem_soat'
      },
      {
        text: 'Nhn hiu. Tn ng k',
        align: 'center',
        sortable: true,
        value: 'nhan_hieu_ten_dk'
      },
      {
        text: 'S khung',
        align: 'center',
        sortable: true,
        value: 'so_khung'
      },
      {
        text: 'S my',
        align: 'center',
        sortable: true,
        value: 'so_may'
      },
      {
        text: 'Loi phng tin',
        align: 'center',
        sortable: true,
        value: 'loai_phuong_tien'
      },
      {
        text: 'Thi im xut / nhp cnh',
        align: 'center',
        sortable: true,
        value: 'tdxnc'
      },
      {
        text: 'Thao tc',
        align: 'center',
        sortable: true,
        value: 'thaoTac'
      }
    ],
    phuongTienItems: [],
    hosoDatasTotal: 1,
    hosoDatasPage: 1,
    keywordsSearchPT: '',
    loadingPhuongTien: false
  }),
  computed: {
    loading () {
      return this.$store.getters.loading
    }
  },
  created () {
    var vm = this
    let data = {
      start: 0,
      end: 15
    }
    let currentQuery = vm.$router.history.current.query
    if (currentQuery.hasOwnProperty('gp') && currentQuery.gp) {
      data['gp'] = true
      vm.$store.commit('setStateTabQl', 'phuong_tien2')
    } else {
      vm.$store.commit('setStateTabQl', 'phuong_tien')
    }
    vm.loadListPhuongTien(data)
  },
  watch: {
    '$route': function (oldRoute, newRoute) {
      let vm = this
      let currentQuery = vm.$router.history.current.query
      let data = {
        start: 0,
        end: 15
      }
      if (currentQuery.hasOwnProperty('gp') && currentQuery.gp) {
        data['gp'] = true
      }
      vm.loadListPhuongTien(data)
    }
  },
  methods: {
    searchPT () {
      var vm = this
      vm.loadListPhuongTien({
        start: 0,
        end: 15
      })
    },
    toDetail (item, param) {
      var vm = this
      let data = {
        path: '/phuong-tien/' + item.bienkiemsoat,
        query: {}
      }
      if (param) {
        data.query[String(param)] = ''
      }
      router.push(data)
    },
    loadListPhuongTien (data) {
      var vm = this
      vm.loadingPhuongTien = true
      let params = {
        keyword: vm.keywordsSearchPT
      }
      if (data !== null && data !== undefined && data !== '') {
        for (var key in data) {
          params[key] = data[key]
        }
      }
      vm.$store.dispatch('loadPhuongTien', params).then(results => {
        vm.phuongTienItems = results.data
        vm.hosoDatasTotal = parseInt(results.total)
        vm.loadingPhuongTien = false
      }).catch(xhr => {
      })
    },
    viewHistoryXNC (item) {
      var vm = this
      vm.registrationNumber = item.bienkiemsoat
      vm.thongTinXNCDialog = true
    },
    paggingData (config) {
      var vm = this
      vm.loadListPhuongTien({
        start: (config.page - 1) * 15,
        end: (config.page - 1) * 15 + 15
      })
      vm.hosoDatasPage = config.page
    }
  },
  filters: {
  }
}
</script>



