<template>
  <div>
    <v-layout row wrap style="background-color: #fff; margin: 0;">
      <v-flex xs12 sm3>
      </v-flex>
      <v-flex xs12 sm6 style="display: inline-flex; ">
        <v-text-field
        placeholder="Tm kim theo m/tn doanh nghip/GCN ng k kinh doanh"
        v-model="keywordsSearchDNVT"
        style="width : 350px;"
        :disabled="loadingDoanhNghiepVanTaiTable"
        @keyup.enter="searchKeyWordDNVT"
        clearable
        ></v-text-field>
        <v-btn small :disabled="loadingDoanhNghiepVanTaiTable" class="" color="primary" @click="searchKeyWordDNVT">
          Tm kim
        </v-btn>
        <v-btn small :disabled="loadingDoanhNghiepVanTaiTable" class="" color="primary" @click="dialogSearch = !dialogSearch">
          Tm kim nng cao
        </v-btn>
      </v-flex>
      <v-flex xs12 sm3>
      </v-flex>
      <v-flex xs12 sm12 class="pt-3">
        <v-data-table
        :headers="doanhNgiepVanTaiHeaders"
        :items="doanhNgiepVanTaiItems"
        :loading="false"
        hide-actions
        class="table-landing table-bordered mx-3"
        no-data-text="Khng c doanh nghip no!"
        id="tableGiayPhepVanTaiQuocTe"
        v-if="!loadingDoanhNghiepVanTaiTable">
        <template slot='headers' slot-scope='props'>
          <tr>
            <th v-for="(header, index) in props.headers" :key="header.text" class="text-xs-center">
              <span>{{ header.text }}</span>
            </th>
          </tr>
        </template>
        <template slot="items" slot-scope="props">
          <td style="padding: 8px; padding-left: 0px;width: 3%; " class="text-xs-center">{{ hosoDatasPage * 15 - 15 + props.index + 1 }}</td>
          <td style="padding: 8px; cursor: pointer;" class="text-xs-left" @click="toDetail(props.item)">
            {{ props.item.ten }}
          </td>
          <td style="padding: 8px; width: 15%;" class="text-xs-center">
            {{ props.item['sogpvt_quocte_null'] }} 
          </td>
          <td style="padding: 8px; width: 15%;" class="text-xs-center">
            {{ props.item['sogpkd_vantai_null'] }} 
          </td>
          <td style="padding: 8px; width: 15%;" class="text-xs-center">
            {{ props.item.sogcn_dkkd }}  
          </td>
          <td style="padding: 8px; width: 9%;" class="text-xs-center">
            <v-tooltip top class="mr-1">
              <v-icon slot="activator" style="font-size: 16px; cursor: pointer" @click="toDetail(props.item, 'phuongtien_anchor')">directions_car</v-icon>
              <span>Phng tin</span>
            </v-tooltip>
            <v-tooltip top class="mr-1">
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer' @click="toDetail(props.item, 'tuyen_anchor')">directions</v-icon>
              <span>Tuyn</span>
            </v-tooltip>
            <v-tooltip top class="mr-1">
              <v-icon slot="activator" style="font-size: 16px; cursor: pointer" @click="toDetail(props.item)">visibility</v-icon>
              <span>Xem</span>
            </v-tooltip>
          </td>
        </template>
      </v-data-table>
      <div v-else>
        <content-placeholders class="mt-1">
          <content-placeholders-text :lines="10" />
        </content-placeholders>
      </div>
      <tiny-pagination class="mr-3" style="float: right;" :total="hosoDatasTotal" :page="hosoDatasPage" custom-class="custom-tiny-class" @tiny:change-page="paggingData"></tiny-pagination>
      </v-flex>
    </v-layout>
  </div>
</template>
<script>
import router from '@/router'
import TinyPagination from './pagging/hanghai_pagination.vue'
import $ from 'jquery'
export default {
  props: ['index'],
  components: {
    'tiny-pagination': TinyPagination
  },
  data: () => ({
    govAgencys: [],
    btnLoading: false,
    doanhNgiepVanTaiHeaders: [
      {
        text: 'STT',
        align: 'center',
        sortable: false,
        value: 'stt'
      },
      {
        text: 'Tn doanh nghip vn ti',
        align: 'left',
        sortable: true,
        value: 'ten_doanh_nghiep'
      },
      {
        text: 'S GP vn ti quc t',
        align: 'center',
        sortable: true,
        value: 'sogpvt'
      },
      {
        text: 'S GP kinh doanh vn ti',
        align: 'center',
        sortable: true,
        value: 'sogpkd'
      },
      {
        text: 'S GCN ng k kinh doanh',
        align: 'center',
        sortable: true,
        value: 'sogcn'
      },
      {
        text: 'Thao tc',
        align: 'center',
        sortable: true,
        value: 'thaoTac'
      }
    ],
    doanhNgiepVanTaiItems: [],
    hosoDatasTotal: 1,
    hosoDatasPage: 1,
    keywordsSearchDNVT: '',
    loadingDoanhNghiepVanTaiTable: false
  }),
  computed: {
    loading () {
      return this.$store.getters.loading
    }
  },
  created () {
    var vm = this
    vm.loadListDoanhNghiepVanTai({
      start: 0,
      end: 15
    })
    vm.$store.commit('setStateTabQl', 'doanh_nghiep_van_tai')
  },
  watch: {
  },
  methods: {
    searchKeyWordDNVT () {
      var vm = this
      vm.loadListDoanhNghiepVanTai({
        start: 0,
        end: 15
      })
    },
    loadListDoanhNghiepVanTai (data) {
      var vm = this
      vm.loadingDoanhNghiepVanTaiTable = true
      let params = {
        keyword: vm.keywordsSearchDNVT
      }
      if (data !== null && data !== undefined && data !== '') {
        params['start'] = data['start']
        params['end'] = data['end']
      }
      vm.$store.dispatch('loadDoanhNghiepVanTai', params).then(results => {
        if (Array.isArray(results.data)) {
          vm.doanhNgiepVanTaiItems = results.data
        } else {
          vm.doanhNgiepVanTaiItems = JSON.parse(results.data)
        }
        vm.hosoDatasTotal = parseInt(results.total)
        vm.loadingDoanhNghiepVanTaiTable = false
      }).catch(xhr => {
        vm.loadingDoanhNghiepVanTaiTable = false
      })
    },
    toDetail (item, param) {
      var vm = this
      let data = {
        path: '/doanh-nghiep-van-tai/' + item.id,
        query: {}
      }
      if (param) {
        data.query[String(param)] = ''
      }
      router.push(data)
    },
    paggingData (config) {
      var vm = this
      vm.loadListDoanhNghiepVanTai({
        start: (config.page - 1) * 15,
        end: (config.page - 1) * 15 + 15
      })
      vm.hosoDatasPage = config.page
    }
  },
  filters: {
    dateTimeView (arg) {
      if (arg) {
        let value = new Date(arg)
        return `${value.getDate().toString().padStart(2, '0')}/${(value.getMonth() + 1).toString().padStart(2, '0')}/${value.getFullYear()} | ${value.getHours().toString().padStart(2, '0')}:${value.getMinutes().toString().padStart(2, '0')}`
      } else {
        return ''
      }
    }
  }
}
</script>



