    loadDanhSachHoSo ({commit, state}, data) {
      return new Promise((resolve, reject) => {
        commit('setLoading', true)
        store.dispatch('loadInitResource').then(function (result1) {
          let paramsTmp = {}
          if (data !== null && data !== undefined && data !== '' && data !== {}) {
            for (var key in data) {
              paramsTmp[key] = data[key]
            }
          }
          let param = {
            headers: {
              groupId: state.initData.groupId
            },
            params: paramsTmp
          }
          axios.get(support.initData.dossierApi, param).then(result => {
            if (result.data.data) {
              let dataTmp = result.data.data
              for (var i = 0; i < dataTmp.length; i++) {
                var createDate = dataTmp[i].createDate.toString()
                var year = createDate.substr(0, 4)
                var month = createDate.substr(4, 2)
                var day = createDate.substr(6, 2)
                var hh = createDate.substr(8, 2)
                var mm = createDate.substr(10, 2)
                var ss = createDate.substr(12, 2)
                dataTmp[i].createDate = day + '/' + month + '/' + year + ' ' + hh + ':' + mm + ':' + ss
              }
              result.data.data = dataTmp
            }
            commit('setDanhSachHoSo', result.data.data)
            commit('setLengthDanhSachHoSo', Math.ceil(result.data.total / 15))
            resolve(result.data)
            commit('setLoading', false)
          }).catch(xhr => {
            commit('setLoading', true)
            reject(xhr)
          })
        })
      })
    },
