    loadDossierAction ({commit, state, dispatch}, data) {
      return new Promise((resolve, reject) => {
        store.dispatch('loadInitResource').then(function (result1) {
          let config = {
            headers: {
              groupId: state.initData.groupId
            }
          }
          var listHistoryProcessingItems = []
          axios.get(support.initData.dossierlogsApi + '/' + data.dossierId + '/logs', config).then(result => {
            var serializable = result.data
            for (var key in serializable.data) {
              if (serializable.data[key].notificationType === 'PROCESS_TYPE') {
                serializable.data[key].createDate = dispatch('parseDateUtc', serializable.data[key].createDate)
                var stepNameTmp = serializable.data[key].payload.stepName
                if (stepNameTmp) {
                  if (stepNameTmp === 'type_cancelling') {
                    stepNameTmp = 'Yu cu hy'
                  } else if (stepNameTmp === 'type_submit') {
                    stepNameTmp = 'Yu cu sa thnh phn h s'
                  } else if (stepNameTmp === 'type_submitting') {
                    stepNameTmp = 'Yu cu sa i b sung'
                  } else if (stepNameTmp === 'type_correcting') {
                    stepNameTmp = 'Yu cu chnh sa kt qu'
                  } else if (stepNameTmp === 'type_reject_cancelling') {
                    stepNameTmp = 'T chi yu cu hy'
                  } else if (stepNameTmp === 'type_reject_submit') {
                    stepNameTmp = 'Hy yu cu sa thnh phn h s'
                  } else if (stepNameTmp === 'type_reject_submitting') {
                    stepNameTmp = 'T chi yu cu b sung'
                  } else if (stepNameTmp === 'type_reject_correcting') {
                    stepNameTmp = 'Hy yu cu chnh sa kt qu'
                  }
                }
                serializable.data[key].payload.stepName = stepNameTmp
                listHistoryProcessingItems.push(serializable.data[key])
              }
            }
            resolve(listHistoryProcessingItems)
          }).catch(xhr => {
            reject(xhr)
          })
        })
      })
    },
