  mutations: {
    setIndex (state, payload) {
      state.index = payload
    },
    setLoading (state, payload) {
      state.loading = payload
    },
    setInitData (state, payload) {
      state.initData = payload
    },
    setDanhSachHoSo (state, payload) {
      state.danhSachHoSoTableItems = payload
    },
    setLengthDanhSachHoSo (state, payload) {
      state.lengthDanhSachHoSo = payload
    },
    setHoSoLableTitle (state, payload) {
      state.hoso_title_table = payload
    },
    setStatusParamFilter (state, payload) {
      state.statusParamFilter = payload
    },
    setSubstatusParamFilter (state, payload) {
      state.substatusParamFilter = payload
    },
    setAlpacaAssignUserId (state, payload) {
      state.alpacaAssignUserId = payload
    },
    setSubUsers (state, payload) {
      state.subUsers = payload
    },
    setStateTabQl (state, payload) {
      state.stateTabQl = payload
    }
  },
  getters: {
    loading (state) {
      return state.loading
    },
    index (state) {
      return state.index
    },
    danhSachHoSo (state) {
      return state.danhSachHoSoTableItems
    },
    lengthDanhSachHoSo (state) {
      return state.lengthDanhSachHoSo
    },
    hoSoLableTitle (state) {
      return state.hoso_title_table
    },
    statusParamFilter (state) {
      return state.statusParamFilter
    },
    substatusParamFilter (state) {
      return state.substatusParamFilter
    },
    alpacaAssignUserId (state) {
      return state.alpacaAssignUserId
    },
    subUsers (state) {
      return state.subUsers
    },
    configPage (state) {
      return state.configPage
    },
    stateTabQl (state) {
      return state.stateTabQl
    },
    initData (state) {
      return state.initData
    }
  }
