import Vue from 'vue/dist/vue.min.js'
import Router from 'vue-router'
import Landing from '@/components/Landing'
import Captcha from '@/components/Captcha'
import DanhSachHoSo from '@/components/DanhSachHoSo'
import DoanhNghiepVanTai from '@/components/DoanhNghiepVanTai'
import DoanhNghiepVanTaiDetail from '@/components/DoanhNghiepVanTaiDetail'
import GiayPhepLienVan from '@/components/GiayPhepLienVan'
import GiayPhepVanTaiQuocTe from '@/components/GiayPhepVanTaiQuocTe'
import PhuHieuLienVan from '@/components/PhuHieuLienVan'
import PhuongTien from '@/components/PhuongTien'
import PhuongTienDetail from '@/components/PhuongTienDetail'
import AddPhuongTien from '@/components/AddPhuongTien'
import TEST from '@/components/TEST'

const routes = [
  // { path: '/', component: TEST, props: true },
  { path: '/', component: DoanhNghiepVanTai, props: true },
  { path: '/doanh-nghiep-van-tai', component: DoanhNghiepVanTai, props: true },
  { path: '/doanh-nghiep-van-tai/:id', component: DoanhNghiepVanTaiDetail, props: true },
  { path: '/giay-phep-lien-van', component: GiayPhepLienVan, props: true },
  { path: '/giay-phep-van-tai-quoc-te', component: GiayPhepVanTaiQuocTe, props: true },
  { path: '/phu-hieu-lien-van', component: PhuHieuLienVan, props: true },
  { path: '/phuong-tien', component: PhuongTien, props: true },
  { path: '/phuong-tien/:id', component: PhuongTienDetail, props: true },
  { path: '/them-phuong-tien/:id', component: AddPhuongTien, props: true }
]

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes
})

export default router



