  mounted () {
    let vm = this
    // let currentParams = vm.$router.history.current.params
    vm.$nextTick(function () {
      let currentQuery = vm.$router.history.current.query
      if (currentQuery.hasOwnProperty('phuongtien_anchor')) {
        $('html, body').animate({
          scrollTop: $('#hopDongThuePhuongTien').offset().top
        }, 700)
      } else if (currentQuery.hasOwnProperty('tuyen_anchor')) {
        $('html, body').animate({
          scrollTop: $('#thongTinTuyenKhaiThac').offset().top
        }, 700)
      }
    })
