  created () {
    var vm = this
    vm.loadThamChieu()
    vm.loadingDoanhNghiepVanTaiTable = true
    vm.$store.dispatch('loadPhuongTienDetail', {
      id: vm.id
    }).then(results => {
      vm.phuongTienDetail = results
      vm.loadingThongTinGPKDVT = true
      vm.$store.dispatch('loadPhuongTienGPPT', {
        bienkiemsoat: vm.phuongTienDetail.bienkiemsoat
      }).then(results => {
        vm.thongTinGPKDVTItems = JSON.parse(results)
        vm.loadingThongTinGPKDVT = false
      }).catch(xhr => {
        vm.loadingThongTinGPKDVT = false
      })
      vm.loadingThongTinHopDongThuePhuongTien = true
      vm.$store.dispatch('loadHopDongThuePhuongTien', {
        id: vm.phuongTienDetail.id
      }).then(results => {
        vm.thongTinHopDongThuePhuongTienItems = results
        vm.loadingThongTinHopDongThuePhuongTien = false
      }).catch(xhr => {
        vm.loadingThongTinHopDongThuePhuongTien = false
      })
      vm.loadingTuyenKhaiThac = true
      vm.$store.dispatch('loadTuyenKhaiThac2', {
        id: vm.phuongTienDetail.id
      }).then(results => {
        vm.tuyenKhaiThacItems = results
        vm.loadingTuyenKhaiThac = false
      }).catch(xhr => {
        vm.loadingTuyenKhaiThac = false
      })
      vm.loadingPhuHieu = true
      vm.$store.dispatch('loadThongTinPhuHieu', {
        id: vm.phuongTienDetail.id
      }).then(results => {
        vm.phuHieucItems = results
        vm.loadingPhuHieu = false
      }).catch(xhr => {
        vm.loadingPhuHieu = false
      })
      vm.loadingthongTinViPham = true
      vm.$store.dispatch('loadThongTinViPham', {
        id: vm.phuongTienDetail.id
      }).then(results => {
        vm.thongTinViPhamItems = results
        vm.loadingthongTinViPham = false
      }).catch(xhr => {
        vm.loadingthongTinViPham = false
      })
    }).catch(xhr => {
    })
    vm.loadListCitys()
    vm.loadListCuaKhau()
    vm.loadListBenXe()
    vm.loadListMucDich()
    vm.loadListHanhTrinh()
    vm.$store.commit('setStateTabQl', 'phuong_tien')
    var today = new Date()
    var dd = today.getDate()
    var mm = today.getMonth() + 1
    var yyyy = today.getFullYear()
    if (dd < 10) {
      dd = '0' + dd
    }
    if (mm < 10) {
      mm = '0' + mm
    }
    let tuNgay = yyyy + '-' + mm + '-' + dd
    vm.dateNC = tuNgay
    var nextMonth
    for (var i = 0; i < 30; i++) {
      nextMonth = new Date(yyyy, mm - 1, dd + i)
    }
    dd = nextMonth.getDate()
    mm = nextMonth.getMonth() + 1
    yyyy = nextMonth.getFullYear()
    let denNgay = yyyy + '-' + mm + '-' + dd
    vm.dateHH = denNgay
  },
