<template>
  <div>
    <v-layout row wrap style="background-color: #fff; margin: 0;">
      <v-flex xs12 sm3>

      </v-flex>
      <v-flex xs12 sm6 style="display: inline-flex; ">
        <v-text-field
        placeholder="Nhp bin kim sot/ S ph hiu"
        v-model="keywordsSearchPHLV"
        style="width : 350px;"
        :disabled="loadingPhuHieuLienVan"
        @keyup.enter="searchKeyWordPHLV"
        clearable
        ></v-text-field>
        <v-btn small :disabled="loadingPhuHieuLienVan" class="" color="primary" @click.native="searchKeyWordPHLV">
          Tm kim
        </v-btn>
        <v-btn small :disabled="loadingPhuHieuLienVan" class="" color="primary" @click.native="dialogSearch = !dialogSearch">
          Tm kim nng cao
        </v-btn>
      </v-flex>
      <v-flex xs12 sm3>

      </v-flex>
      <v-flex xs12 sm12 class="mt-3">
        <v-data-table
        :headers="phuHieuLienVanHeaders"
        :items="phuHieuLienVanItems"
        :loading="false"
        hide-actions
        class="table-landing table-bordered mx-3"
        no-data-text="Khng c phiu lin vn no!"
        v-if="!loadingPhuHieuLienVan">
        <template slot='headers' slot-scope='props'>
          <tr>
            <th v-for="(header, index) in props.headers" :key="header.text" class="text-xs-center">
              <span>{{ header.text }}</span>
            </th>
          </tr>
        </template>
        <template slot="items" slot-scope="props">
          <td style="padding: 8px; padding-left: 0px;width: 3%; " class="text-xs-center">{{ hosoDatasPage * 15 - 15 + props.index + 1 }}</td>
          <td style="padding: 8px; width: 15%;" class="text-xs-center">
            {{ props.item.sophuhieu }} 
          </td>
          <td style="padding: 8px; width: 15%;" class="text-xs-center">
            {{ props.item.noicap }} 
          </td>
          <td style="padding: 8px; width: 15%;" class="text-xs-center">
            {{ props.item.ngaycap }} 
          </td>
          <td style="padding: 8px;" class="text-xs-center">
            {{ props.item.ngayhethan }}  
          </td>
          <td>
            {{ props.item.donvikhaithac }} 
          </td>
          <td style="padding: 8px;" class="text-xs-center">
            {{ props.item.bienkiemsoat }} <br>
            {{ props.item.loaiphuongtien }} 
          </td>
          <td style="padding: 8px; width: 15%;" class="text-xs-center">
            <v-tooltip top class='mr-1'>
              <a :href="props.item.download" target="_blank" slot="activator">
                <v-icon style="font-size: 18px; cursor: pointer; font-weight: bold;">vertical_align_bottom</v-icon>
              </a>
              <span>Download</span>
            </v-tooltip> 
          </td>
          
          <!-- <td style="padding: 8px; width: 10%;" class="text-xs-center">
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer'>directions_car</v-icon>
              <span>Phng tin</span>
            </v-tooltip>
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer'>directions</v-icon>
              <span>Tuyn</span>
            </v-tooltip>
            <v-tooltip top class='mr-1'>
              <v-icon slot='activator' style='font-size: 16px; cursor: pointer'>visibility</v-icon>
              <span>Xem</span>
            </v-tooltip>
          </td> -->
        </template>
      </v-data-table>
      <div v-else>
        <content-placeholders class="mt-1">
          <content-placeholders-text :lines="10" />
        </content-placeholders>
      </div>
      <tiny-pagination v-if="!loadingPhuHieuLienVan" style="float: right;" :total="hosoDatasTotal" :page="hosoDatasPage" class="mx-3" custom-class="custom-tiny-class" 
          @tiny:change-page="paggingData" ></tiny-pagination>
      <div v-else>
        <content-placeholders class="mt-1">
          <content-placeholders-text :lines="1" />
        </content-placeholders>
      </div>
      </v-flex>
    </v-layout>
  </div>
</template>
<script>
import router from '@/router'
import TinyPagination from './pagging/hanghai_pagination.vue'
export default {
  props: ['index'],
  components: {
    'tiny-pagination': TinyPagination
  },
  data: () => ({
    govAgencys: [],
    btnLoading: false,
    phuHieuLienVanHeaders: [
      {
        text: 'STT',
        align: 'center',
        sortable: false,
        value: 'stt'
      },
      {
        text: 'S ph hiu',
        align: 'center',
        sortable: true,
        value: 'so_phu_hieu'
      },
      {
        text: 'Ni cp',
        align: 'center',
        sortable: true,
        value: 'sogpvt'
      },
      {
        text: 'Ngy cp',
        align: 'center',
        sortable: true,
        value: 'ngay_cap'
      },
      {
        text: 'Ngy ht hn',
        align: 'center',
        sortable: true,
        value: 'ngay_het_han'
      },
      {
        text: 'n v khai thc',
        align: 'center',
        sortable: true,
        value: 'don_vi_khai_thac'
      },
      {
        text: 'Bin kim sot. Loi phng tin',
        align: 'center',
        sortable: true,
        value: 'bkslpt'
      },
      {
        text: 'Download',
        align: 'center',
        sortable: true,
        value: 'download'
      }
      // {
      //   text: 'Thao tc',
      //   align: 'center',
      //   sortable: true,
      //   value: 'thaoTac'
      // }
    ],
    phuHieuLienVanItems: [],
    hosoDatasTotal: 1,
    hosoDatasPage: 1,
    keywordsSearchPHLV: '',
    loadingPhuHieuLienVan: false
  }),
  computed: {
    loading () {
      return this.$store.getters.loading
    }
  },
  created () {
    var vm = this
    vm.loadListPhuHieuLienVan({
      start: 0,
      end: 15
    })
    vm.$store.commit('setStateTabQl', 'phu_hieu_lien_van')
  },
  watch: {
  },
  methods: {
    searchKeyWordPHLV () {
      var vm = this
      vm.loadListPhuHieuLienVan({
        start: 0,
        end: 15
      })
    },
    loadListPhuHieuLienVan (data) {
      var vm = this
      vm.loadingPhuHieuLienVan = true
      let params = {
        keyword: vm.keywordsSearchPHLV
      }
      if (data !== null && data !== undefined && data !== '') {
        params['start'] = data['start']
        params['end'] = data['end']
      }
      vm.$store.dispatch('loadPhuHieuLienVan', params).then(results => {
        vm.phuHieuLienVanItems = results.data
        vm.hosoDatasTotal = parseInt(results.total)
        vm.loadingPhuHieuLienVan = false
      }).catch(xhr => {
      })
    },
    paggingData (config) {
      var vm = this
      vm.loadListPhuHieuLienVan({
        start: (config.page - 1) * 15,
        end: (config.page - 1) * 15 + 15
      })
      vm.hosoDatasPage = config.page
    }
  },
  filters: {
  }
}
</script>



