                clearable
                ></v-select>
              </v-flex>
              <v-flex xs12 sm6 class="pl-3">
                <v-text-field
                v-model="dossierNo"
                label="M tip nhn"
                clearable
                ></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 class="pl-4">
                <v-text-field
                v-model="referenceUid"
                label="M s h s"
                clearable
                ></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 class="text-xs-right">
                <v-btn color="primary" small class="mt-3" @click.native="searchAdvanceDossier">Tm kim</v-btn>
              </v-flex>
            </v-layout>
          </v-card-text>
            </v-card>
          </v-dialog>
          <!-- <v-dialog
            v-model="dialogViewFile"
            scrollable 
