          <v-dialog
            v-model="dialogViewLogs"
            scrollable 
            persistent :overlay="false"
            max-width="900px"
            transition="dialog-transition"
          >
            <v-card>
              <v-card-title class="px-0 py-0" style="height: 50px;">
                <v-toolbar dark color="primary" height="40">
                  <div class="text-bold">Tin nhn trao i</div>
                  <v-spacer></v-spacer>
                  <v-toolbar-items>
                    <v-btn icon dark @click.native="dialogViewLogs = false">
                      <v-icon>close</v-icon>
                    </v-btn>
                  </v-toolbar-items>
                </v-toolbar>
              </v-card-title>
              <v-card-text class="pt-0 px-0 py-0">
                <div v-if="listHistoryProcessingItems.length > 0" class="table__overflow elevation-1 table_history_style">
                  <v-data-table
                  :items="listHistoryProcessingItems"
                  hide-headers
                  hide-actions>
                    <template slot="items" slot-scope="props">
                      <td style="padding-top: 15px;">{{ props.index + 1 }}</td>
                      <td style="padding-top: 15px;" class="text-xs-left">
                        <p> ng/b <b>{{ props.item.author }}</b> 
                          <span class="text-blue">( {{ props.item.payload.stepName }} )</span>
                          <br/>
                          <span class="text-light-gray">{{ props.item.createDate }}</span>
                        </p>
                        <span> kin: </span> <span v-html="props.item.content"></span> <br>
                      </td>
                    </template>
                  </v-data-table>
                </div>
                <div v-else class="text-xs-center center-all" style="height: 500px; width: 100%;">
                  <v-progress-circular indeterminate v-bind:size="100" color="purple"></v-progress-circular>
                </div>
                
              </v-card-text>
            </v-card>
          </v-dialog>
        </v-flex>
        <v-flex xs12 sm12>
          <v-data-table
          :headers="danhSachHoSoTableheaders"
          :items="danhSachHoSo"
          hide-actions
          class="table-landing table-bordered"
          no-data-text="Khng c h s no!"
          id="danhSachHoSoTable"
          v-if="!loading">
          <template slot="items" slot-scope="props">
            <td style="padding: 8px; padding-left: 0px;width: 3%;" class="text-xs-center">{{ danhSachHoSoTablepage * 15 - 15 + props.index + 1 }}</td>
            <td style="padding: 8px; width: 10%;" class="text-xs-center">
              {{ props.item.referenceUid }}
            </td>
            <td style="padding: 8px; width: 39%;" class="text-xs-left">
              <a href="javascript:;" @click.prevent.stop="toDetailHoSo(props.item)">
                {{ props.item.serviceName }} 
                <br>
              </a>
              {{ props.item.applicantName }} 
            </td>
            <td style="padding: 8px; width: 12%;" class="text-xs-left">
              <span v-if="props.item.dossierSubStatusText">{{ props.item.dossierSubStatusText }}</span>
              <span v-else>{{ props.item.dossierStatusText }}</span>
            </td>
            <td style="padding: 8px; width: 30%; position: relative;" class="text-xs-left">
              {{ props.item.receiveDate }} : Np h s <br>
              <span style="color: #da8f05;">{{ props.item.lastActionDate }} : <i>{{subLastActionUserName(props.item['lastActionUserEmail'])}} -> {{ props.item.lastActionName }}</i></span> <br> <br>
              <div style="text-align: left; left: 5px; bottom: 0; position: absolute;">
                {{ props.item.dossierNo }} 
              </div>
              <div style="text-align: right; right: 5px; bottom: 0; position: absolute;">
                <span class="text-hover-blue" @click="viewDialogLog(props.item)" style="color: rgb(134, 131, 131); cursor: pointer;"><i class="fa fa-comments" aria-hidden="true"></i> Tin nhn ({{ props.item['countLogs'] ? props.item['countLogs'] : 0 }})</span>
              </div>
            </td>
          </template>
        </v-data-table>
        <div v-else-if="loading" class="text-xs-center" style="width: 100%;">
          <content-placeholders class="mt-1">
            <content-placeholders-text :lines="10" />
          </content-placeholders>
        </div>
        <v-pagination
        style="float: right;"
        v-model="pageDanhSachHoSo"
        :length="lengthDanhSachHoSo"
        :disabled="loading"
        ></v-pagination>
        </v-flex>
      </v-layout>
    </v-slide-x-transition>
  </div>
</template>
<script type="text/javascript">
  import router from '@/router'
  export default {
    props: ['index'],
    components: {
    },
    data: () => ({
      danhSachHoSoTableheaders: [
        {
          text: 'STT',
          align: 'center',
          sortable: false,
          value: 'stt'
        },
        {
          text: 'M tip nhn',
          align: 'center',
          sortable: false,
          value: 'dossierId'
        },
        {
          text: 'Thng tin h s',
          align: 'center',
          sortable: true,
          value: 'dossierDetail'
        },
        {
          text: 'Trng thi',
          align: 'left',
          sortable: true,
          value: 'status'
        },
        {
          text: 'Ghi ch',
          align: 'left',
          sortable: true,
          value: 'note'
        }
      ],
      loadingDanhSachHoSoTable: false,
      pageDanhSachHoSo: 1,
      keywordsSearch: '',
      dialogSearch: false,
      serviceInfoFilterItems: [],
      serviceInfo: '',
      applicantNameFilterItems: [],
      applicantIdNo: '',
      startDateMenu: false,
      startDate: '',
      endDateMenu: false,
      endDate: '',
      dossierStatusItems: [],
      dossierStatus: '',
      dossierNo: '',
      referenceUid: '',
      dialogViewLogs: false,
      listHistoryProcessingItems: [],
      agencies: ''
    }),
    computed: {
      loading () {
        return this.$store.getters.loading
      },
      danhSachHoSo () {
        return this.$store.getters.danhSachHoSo
      },
      lengthDanhSachHoSo () {
        return this.$store.getters.lengthDanhSachHoSo
      },
      statusParamFilter () {
        return this.$store.getters.statusParamFilter
      },
      substatusParamFilter () {
        return this.$store.getters.substatusParamFilter
      }
    },
    created () {
      var vm = this
      vm.loadingDanhSachHoSo()
      vm.$store.dispatch('loadMenuBestUser', null).then(results => {
        vm.serviceInfoFilterItems = results
      })
      vm.$store.dispatch('loadServiceInfos', null).then(results => {
        vm.serviceInfoFilterItems = results
      })
      vm.$store.dispatch('loadApplicantNamesSearch', null).then(results2 => {
        vm.applicantNameFilterItems = results2
      })
      vm.$store.dispatch('loadDossierStatusSearch', null).then(results3 => {
        vm.dossierStatusItems = results3
      })
    },
    watch: {
      pageDanhSachHoSo (value) {
        var vm = this
        let start = (value - 1) * 15
        let end = (value - 1) * 15 + 15
        vm.loadingDanhSachHoSo({
          start: start,
          end: end
        })
      },
      '$route': function (newRoute, oldRoute) {
        let vm = this
        let currentQuery = newRoute.query
        if (currentQuery.hasOwnProperty('reloadList')) {
          vm.loadingDanhSachHoSo()
        }
      }
    },
    methods: {
      loadingDanhSachHoSo (data) {
        var vm = this
        vm.loadingDanhSachHoSoTable = true
        let params = {
          keyword: vm.keywordsSearch,
          owner: vm.applicantIdNo,
          service: vm.serviceInfo,
          agency: vm.agencies,
          follow: true,
          dossierNo: vm.dossierNo,
          referenceUid: vm.referenceUid,
          dossierStatus: vm.dossierStatus,
          start: vm.pageDanhSachHoSo * 15 - 15,
          end: vm.pageDanhSachHoSo * 15,
          status: vm.statusParamFilter,
          substatus: vm.substatusParamFilter,
          startDate: vm.parseDate(vm.startDate),
          endDate: vm.parseDate(vm.endDate),
          sort: 'modified',
          order: 'false'
        }
        if (data !== null && data !== undefined && data !== '') {
          for (var key in data) {
            params[key] = data[key]
          }
        }
        vm.$store.dispatch('loadDanhSachHoSo', params).then(results => {
          vm.loadingDanhSachHoSoTable = false
        }).catch(xhr => {
        })
      },
      searchKeyWord () {
        var vm = this
        vm.loadingDanhSachHoSo()
      },
      searchAdvanceDossier () {
        var vm = this
        vm.loadingDanhSachHoSo()
      },
      viewDialogLog () {
        var vm = this
      },
      toDetailHoSo (item) {
        var vm = this
        router.push('/danh-sach-ho-so/' + vm.index + '/chi-tiet/' + item.dossierId)
      },
      parseDate (date) {
        if (!date) return null
        const [year, month, day] = date.split('-')
        return `${day}/${month}/${year}`
      }
    },
    filters: {
    }
  }
</script>



