<template>
  <div style="background: #ffff;">
    <v-layout row wrap class="ml-0">
      <v-flex xs12 sm12 class="mb-3 mt-3">
        <v-data-table :headers="vehicleHeaders" :items="vehicleItems" item-key="name" hide-actions expand class="table-landing table-bordered mx-3">
          <template slot="items" scope="props">
            <tr @click="props.expanded = !props.expanded">
              <td class="text-xs-left">{{ props.item.name }}</td>
              <td class="text-xs-left">{{ props.item.value }}</td>
            </tr>
          </template> 
        </v-data-table>               
      </v-flex>
      <v-flex xs12 sm12>
        <v-subheader class="mt-2" style="color: blue; float: left;">LCH S XUT NHP CNH</v-subheader>
        <v-btn class="mr-3" style="float: right;" color="primary" small @click="showAddHistory = !showAddHistory">Thm thng tin xut nhp cnh</v-btn>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-if="showAddHistory" class="ml-0">
      <v-flex xs12 sm2>
        <v-subheader class="mt-2">Chn hnh thc</v-subheader>
      </v-flex>
      <v-flex xs12 sm5 class="pr-3">
        <v-select
          :items="expImpGateTypeItems"
          item-text="itemName"
          item-value="itemCode"
          v-model="expImpGateType"
        ></v-select>
      </v-flex>
      <v-flex xs12 sm5>
        
      </v-flex>
      <v-flex xs12 sm2>
        <v-subheader class="mt-2">Ca khu</v-subheader>
      </v-flex>
      <v-flex xs12 sm5>
        <v-select
          :items="expImpGateItems"
          item-text="itemName"
          item-value="itemCode"
          v-model="expImpGate"
          return-object
          class="pr-4"
        ></v-select>
      </v-flex>
      <v-flex xs12 sm5 class="pr-3">
        <v-menu
          ref="registrationDateMenu"
          :close-on-content-click="false"
          v-model="registrationDateMenu"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          max-width="290px"
          min-width="290px"
        >
          <v-text-field
            slot="activator"
            v-model="registrationDate"
            placeholder="Thi gian"
            persistent-hint
            prepend-icon="event"
          ></v-text-field>
          <v-date-picker v-model="registrationDatePT" no-title @input="$refs.registrationDateMenu.save(registrationDatePT)"></v-date-picker>
        </v-menu>
      </v-flex>
      <v-flex xs12 sm2>
        <v-subheader class="mt-2">Tn li xe</v-subheader>
      </v-flex>
      <v-flex xs12 sm10 class="pr-3">
        <v-text-field
          v-model="driverName"
        ></v-text-field>
      </v-flex>
      <v-flex xs12 sm2>
        <v-subheader class="mt-2">Giy php li xe</v-subheader>
      </v-flex>
      <v-flex xs12 sm10 class="pr-3">
        <v-text-field
          v-model="driverLicenceNo"
        ></v-text-field>
      </v-flex>
      <v-flex xs12 sm12 class="text-xs-center mb-4">
        <v-btn color="primary" small @click="addThongTinXe">
          <v-icon>add</v-icon>
          Thm
        </v-btn>
      </v-flex>
    </v-layout>
    <v-layout row wrap class="ml-0">
      <v-flex xs12 sm12>
        <v-data-table
        :headers="lichSuXNCHeaders"
        :items="lichSuXNCItems"
        hide-actions
        v-if="!loadingLSXNC"
        class="table-landing table-bordered mx-3"
        no-data-text="Khng c phng tin no!">
        <template slot="items" slot-scope="props">
          <td style="padding: 8px; width: 4%;" class="text-xs-center">{{ptDatasPage * 15 - 15 + props.index + 1}}</td>
          <td style="padding: 8px;" class="text-xs-center">{{prop.item.expImpGate}}</td>
          <td style="padding: 8px;" class="text-xs-center">{{prop.item.driverName}}</td>
          <td style="padding: 8px;" class="text-xs-center">{{prop.item.driverLicenceNo}}</td>
          <td style="padding: 8px;" class="text-xs-center">
            <span v-if="prop.item.expImpGateType === 'NC'">Nhp cnh</span>
            <span v-if="prop.item.expImpGateType === 'XC'">Xut cnh</span>
          </td>
          <td style="padding: 8px;" class="text-xs-center">{{prop.item.registrationDate}}</td>
        </template>
      </v-data-table>
      <div v-else>
        <content-placeholders class="mt-1">
          <content-placeholders-text :lines="10" />
        </content-placeholders>
      </div>
      <tiny-pagination class="mt-2 mx-3 mb-3" style="float: right;" :total="ptDatasTotal" :page="ptDatasPage" custom-class="custom-tiny-class" 
      @tiny:change-page="paggingData" ></tiny-pagination>
      </v-flex>
    </v-layout>
  </div>
</template>
<script>
import router from '@/router'
import TinyPagination from './pagging/hanghai_pagination.vue'
export default {
  props: ['id'],
  components: {
    'tiny-pagination': TinyPagination
  },
  data: () => ({
    phuongTienModel: {},
    registrationDateMenu: false,
    registrationDate: '',
    registrationDatePT: '',
    driverName: '',
    driverLicenceNo: '',
    showAddHistory: false,
    expImpGateTypeItems: [
      {
        itemCode: 'XC',
        itemName: 'Xut cnh'
      },
      {
        itemCode: 'NC',
        itemName: 'Nhp cnh'
      }
    ],
    expImpGateType: '',
    expImpGateItems: [],
    expImpGate: {},
    lichSuXNCHeaders: [
      {
        text: 'STT',
        align: 'center',
        sortable: false,
        value: 'stt'
      },
      {
        text: 'Ca khu',
        align: 'center',
        sortable: true,
        value: 'cua_khau'
      },
      {
        text: 'Thng tin li xe',
        align: 'center',
        sortable: true,
        value: 'thong_tin_lai_xe'
      },
      {
        text: 'Giy php li xe',
        align: 'center',
        sortable: true,
        value: 'giay_phep_lai_xe'
      },
      {
        text: 'Hnh thc',
        align: 'center',
        sortable: true,
        value: 'hinh_thuc'
      },
      {
        text: 'Ngy nhp/xut',
        align: 'center',
        sortable: true,
        value: 'ngay_nx'
      }
    ],
    lichSuXNCItems: [],
    loadingLSXNC: false,
    ptDatasTotal: 1,
    ptDatasPage: 1,
    vehicleHeaders: [
      { text: 'Thng tin', value: 'name' },
      { text: 'Gi tr', value: 'value' }
    ],
    vehicleItems: [],
    modelVehicle: {}
  }),
  computed: {
    loading () {
      return this.$store.getters.loading
    }
  },
  created () {
  },
  watch: {
    registrationDatePT (val) {
      this.registrationDate = this.formatDate(this.registrationDatePT)
    },
    id (val) {
      if (val) {
        var vm = this
        vm.$store.dispatch('loadCuaKhau', null).then(results => {
          vm.expImpGateItems = results
        })
        vm.loadPhuongTienModel()
        vm.loadVehicle()
        vm.$store.commit('setStateTabQl', 'phuong_tien')
      }
    }
  },
  methods: {
    paggingData (config) {
      var vm = this
      vm.loadListPhuongTien({
        start: (config.page - 1) * 15,
        end: (config.page - 1) * 15 + 15
      })
      vm.ptDatasPage = config.page
    },
    addThongTinXe () {
      var vm = this
      vm.$store.dispatch('addThongTinXe', {
        registrationNumber: vm.phuongTienModel['registrationNumber'],
        registrationDate: vm.registrationDate,
        driverName: vm.driverName,
        driverLicenceNo: vm.driverLicenceNo,
        expImpGateType: vm.expImpGateType,
        expImpGate: vm.expImpGate.itemName,
        expImpGateCode: vm.expImpGate.itemCode
      }).then(results => {
        vm.loadPhuongTienModel()
      })
    },
    loadPhuongTienModel () {
      var vm = this
      vm.$store.dispatch('getDetailThongTinXe', {
        registrationNumber: vm.id
      }).then(results => {
        vm.phuongTienModel = results
        vm.lichSuXNCItems = results.data
        vm.ptDatasTotal = results.data['length'] ? results.data['length'] : 0
      })
    },
    loadVehicle () {
      var vm = this
      vm.$store.dispatch('getListVehicle', {
        registrationNumber: vm.id
      }).then(results => {
        vm.vehicleItems = results['vehicleItems']
        vm.modelVehicle = results['modelVehicle']
      })
    },
    formatDate (date) {
      console.log('--------date++++++++', date)
      if (!date) return null
      const [year, month, day] = date.split('-')
      return `${day.padStart(2, '0')}/${month.padStart(2, '0')}/${year}`
    }
    // parseDate (date) {
    //   if (!date) return null
    //   console.log(date)
    //   const [month, day, year] = date.split('/')
    //   return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    // }
  },
  filters: {
  }
}
</script>



